<template>
     <section class="complete-radio"
              :class="'complete-radio--' + size">
        <span class="iconfont"
              :class="[isCompleted ? 'icon-complete' : 'icon-uncomplete']"
              @click="onClick"/>
        <slot></slot>
    </section>
</template>

<script>
export default {
    name: 'CompleteRadio',
    props: {
        isCompleted: {
            type: Boolean,
            default: false
        },
        size: {
            type: String,
            default: 'normal',
            validator: val => ['large', 'normal', 'small'].includes(val)
        }
    },

    methods: {
        onClick (event) {
            this.$emit('click', event);
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '~@/assets/styles/mixin.scss';
    @import '~@/assets/styles/variable.scss';
    .complete-radio {
        display: flex;
        align-items: center;

        .iconfont {
            font-size: toRem(25px);
            margin-right: toRem(10px);

            &.icon-uncomplete {
                color: $color-uncomplete;
            }
            &.icon-complete {
                color: $color-complete;
            }
        }

        &--large {
            .iconfont {
                font-size: toRem(32px);
            }
        }

        &--small {
            .iconfont {
                font-size: toRem(18px);
            }
        }
    }
</style>