<template>
  <div class="search-wrap">
    <input type="search" class="search-input" v-model="searchKey">
    <i class="iconfont icon-search" @click="doSearch"></i>
  </div>
</template>

<script>
export default {
  data () {
    return {
      searchKey: ''
    };
  },
  
  watch: {
    searchKey() {
      this.doSearch();
    }
  },

  methods: {
    doSearch() {
      this.$emit('search', this.searchKey);
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/styles/mixin.scss';
