.search-wrap {
  position: relative;
  .search-input {
    display: block;
    width: 100%;
    padding: toRem(5px) toRem(40px) toRem(5px) toRem(10px);
    font-size: 14px;
    line-height: toRem(30px);
    box-sizing: border-box;
    outline: none;
  }
  .iconfont {
    position: absolute;
    right: toRem(8px);
    top: 50%;
    font-size: toRem(24px);
    color: $font-gray-deep;
    transform: translateY(-50%);
    cursor: pointer;
  }
}
</style>
