/**
 * @file 存储相关
 */

import {STORAGE_KEY, STORAGE_TIME_KEY} from './const';

const UPDATE_GRAP = 60 * 60 * 24 * 1000;

let timer = null;

/**
 * 设置storage
 * @param {*} key 
 * @param {*} val 
 */
function setStorage(key, val) {
  if (typeof val === 'string') {
    localStorage.setItem(key, val);
  } else {
    try {
      val = JSON.stringify(val);
      localStorage.setItem(key, val);
    } catch (error) {
      //
    }
  }
}

function getStorage(key) {
  try {
    return JSON.parse(localStorage.getItem(key));
  } catch (error) {
    //
  }
}

/**
 * 每日更新
 */
function autoUpdateStorage () {
  let now = new Date(), lastTime;

  // 如果没有就设置默认值
  if (!getStorage(STORAGE_KEY)) {
    setStorage(STORAGE_KEY, []);
  }

  // 最后一次使用的日期
  if (!getStorage(STORAGE_TIME_KEY)) {
    setStorage(STORAGE_TIME_KEY, now.getTime());
  }

  lastTime = new Date(getStorage(STORAGE_TIME_KEY));

  // 每次打开应用检查是否需要更新数据,间隔大于1天，或者日期发生了变化
  if (now.getTime() - lastTime.getTime() > UPDATE_GRAP || lastTime.getDate() !== now.getDate()) {
    updateList();
    setStorage(STORAGE_TIME_KEY, now.getTime());
  }
}

function updateList() {
  let list = getStorage(STORAGE_KEY), time = new Date();

  list.forEach((task) => {
    
    // 只清算前一天创建的任务
    if (time.getTime() - task.createTime > UPDATE_GRAP || new Date(task.createTime).getDate() !== time.getDate()) {

      // 每日重复的将其置为未完成
      if (task.repeat) {
        task.complete = 0;
      } else {

        // 单日任务到点清除
        task.needDel = true;
      }
    }

  });

  list = list.filter(item => !item.needDel);

  setStorage(STORAGE_KEY, list);
}

function stopAutoUpdate() {
  clearInterval(timer);
}

export {
  setStorage,
  getStorage,
  autoUpdateStorage,
  stopAutoUpdate
}