<template>
  <c-slide-transition-group tag="ul" class="list">
    <li 
      class="list-item"
      v-for="(item, index) in list" 
      :data-index="index" 
      :key="item.id"
      :class="{'list-item--complete': item.complete}">

      <c-collapse-item :disabled="!item.detail && !item.children.length">
        <div class="content-left" slot="title">
          <complete-radio class="content-title" 
                          :is-completed="!!item.complete"
                          @click.stop="updateComplete(index)">
            {{ item.title }}
          </complete-radio>

          <div class="content-tags">
            <c-tag text="长期" color="blue" v-if="item.repeat === 1"/>
            <c-tag :text="getLevel(item).text" :color="getLevel(item).color"/>
            <c-tag 
              v-for="(tag, index) in item.tags" 
              :key="index"
              :text="tag.text"/>
          </div>
        </div>

        <div class="content-right" slot="options" @click.stop="alertOption(index)">
          <div class="circle-wrap">
            <i class="btn-circle"></i>
            <i class="btn-circle"></i>
            <i class="btn-circle"></i>
          </div>
        </div>

        <div class="item-detail">
          <template v-if="item.detail">
            <h2 class="item-detail_title">详情</h2>
            <p class="item-detail_text">{{item.detail}}</p>
          </template>
          <div class="item-detail_subs" v-if="item.children && item.children.length">
            <h2 class="item-detail_title">子任务</h2>
            <div class="item-detail_subs-item" v-for="(child, subIndex) in item.children" 
                                   :key="child.id">
              <complete-radio :is-completed="child.isCompleted"
                              size="small"
                              @click.stop="updateChildComplete(index, subIndex)">
                {{ child.text }}
              </complete-radio>
            </div>
          </div>
        </div>
      </c-collapse-item>
    </li>
  </c-slide-transition-group>
</template>

<script>
import CSlideTransitionGroup from '@/components/common/transition/slide-transition-group';
import CompleteRadio from '@/components/common/complete-radio/index.vue';
import {TASK_LEVEL_MAP} from '@/assets/js/const';

export default {
  name: 'List',

  components: {
    CSlideTransitionGroup,
    CompleteRadio
  },

  props: {
    list: {
      type: Array,
      default() {
        return {};
      }
    }
  },

  methods: {
    getLevel(item) {
      return TASK_LEVEL_MAP[item.level];
    },

    // 收展详情
    toggle(index) {
      this.$emit('toggle', index);
    },

    // 更新完成情况
    updateComplete(index) {
      this.$emit('update-complete', index);
    },

    // 弹出选项按钮
    alertOption(index) {
      this.$emit('alert-option', index);
    },

    del(index) {
      this.list.splice(index, 1);
    },

    updateChildComplete (index, subIndex) {
      this.$emit('update-sub-complete', index, subIndex);
    },

    beforeAppear(el) {
      el.style.opacity = 0;
    }
  }
  
}
</script>

