    }

    .content-left {
      position: relative;
      flex: 1;
      padding: toRem(12px) 0 toRem(12px) toRem(10px);
      border-left: toRem(3px) solid $finished-color;
      .content-title {
        position: relative;
        font-size: 18px;
        line-height: toRem(30px);
        color: $font-black;
      }

      .content-tags {
        display: flex;
        align-items: center;
        margin-top: toRem(20px);
      }
    }
    
    .content-right {
      display: flex;
      align-items: center;
      height: 100%;
      .circle-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: toRem(15px);
        height: toRem(20px);
        .btn-circle {
          display: inline-block;
          width: toRem(5px);
          height: toRem(5px);
          border-radius: 50%;
          background: $font-gray-deep;
        }
      }
    }

    .item-detail {
      padding: toRem(10px) 0 toRem(25px) 0;
