<template>
  <div class="wrap">
    <header class="header">
      <c-progress 
        class="header-progress"
        v-if="showProgress"
        :width="50" 
        :percentage="percentage" 
        type="circle"
        :color="customColors"/>
      
      <search @search="doSearch" class="header-search"/>
    </header>
    <div class="list-wrap">
      <task-list 
        :list="filterList"
        @toggle="toggle"
        @update-complete="updateComplete"
        @alert-option="alertOption"
        @update-sub-complete="updateSubComplete" />
    </div>

    <div class="add-btn" @click="addTask()">
      <i class="iconfont icon-add"></i>
    </div>

    <transition name="hide">
      <div class="mask" v-show="showOptions" @click="cancel()"></div>
    </transition>
    <transition name="slide">
      <div class="options" v-show="showOptions">
        <button class="btn" @click="delTask()">删除</button>
        <button class="btn" @click="editTask()">编辑</button>
        <button class="btn" @click="cancel()">取消</button>
      </div>
    </transition>

    <add-win ref="win" @submit="initData"/>
  </div>
</template>

<script>
import TaskList from '@/components/list/index';
import AddWin from '@/components/addWin/index';
import Search from '@/components/search/index';
import {STORAGE_KEY} from '@/assets/js/const';
import {setStorage, getStorage} from '@/assets/js/storage';

export default {
  name: 'Index',

  components: {
    TaskList,
    AddWin,
    Search
  },

  data () {
    return {
      curIndex: null,
      showOptions: false,
      list: [],
      searchKey: '',
      customColors: [
        {color: '#F56C6C', percentage: 25},
        {color: '#E6A23C', percentage: 50},
        {color: '#67C23A', percentage: 75},
        {color: '#409EFF', percentage: 100}
      ]
    }
  },

  computed: {
    filterList() {
      return this.list.filter(item => (item.title.indexOf(this.searchKey) !== -1));
    },

    showProgress() {
      return this.list.length;
    },

    percentage() {
      let {complete, total} = this.list.reduce((calc, curr) => {
        if (!curr.children || !curr.children.length) {
          calc.total += 1;
          if (curr.complete) {
            calc.complete += 1;
          }

          return calc;
        }

        calc.total += curr.children.length;
        calc.complete += curr.children.filter(item => item.isCompleted).length;
        return calc;
      }, {complete: 0, total: 0});

      return Math.round(complete / total * 100);
    }
  },

  created() {
    this.initData();
  },

  mounted() {
    this.win = this.$refs && this.$refs.win;
  },

  methods: {
    initData() {
      if (!getStorage(STORAGE_KEY)) {
        setStorage(STORAGE_KEY, []);
      }
      this.list = getStorage(STORAGE_KEY);
    },

    // 收展详情
    toggle(index) {
      index = this.getOriginIndex(index);
      let item = this.list[index];
      item.open = Number(!item.open);

      setStorage(STORAGE_KEY, this.list);
    },

    // 更新完成情况
    updateComplete(index, defautlVal) {
      index = this.getOriginIndex(index);
      let item = this.list[index];
      let hasDefault = typeof defautlVal !== 'undefined';

      item.complete = Number(hasDefault ? defautlVal : !item.complete);
      if (item.complete) {
        this.list.splice(index, 1);
        this.list.push(item);
      } else {
        this.list.splice(index, 1);
        this.list.unshift(item);
      }

      if (!hasDefault) {
          this.list[index].children.forEach(subitem => {
            subitem.isCompleted = !!item.complete;
          });
        }

      setStorage(STORAGE_KEY, this.list);
    },

    updateSubComplete (index, subIndex) {
      let children = this.list[index].children;
      let isCompleted = children[subIndex].isCompleted;
      this.list[index].children[subIndex].isCompleted = !isCompleted;
      setStorage(STORAGE_KEY, this.list);

      if (children.every(item => item.isCompleted)) {
        this.updateComplete(index, true);
      } else {
        this.updateComplete(index, false);
      }
    },

    // btns
    alertOption(index) {
      this.curIndex = index;
      this.showOptions = true;
    },

    delTask() {
      let index = this.getOriginIndex(this.curIndex);
      this.list.splice(index, 1);

      setStorage(STORAGE_KEY, this.list);
      this.cancel();
    },

    addTask() {
      this.win.add();
    },

    editTask() {
      this.win.edit(this.list[this.getOriginIndex(this.curIndex)]);
      this.cancel();
    },

    cancel() {
      this.showOptions = false;
    },

    getOriginIndex(index) {
      var originIndex, task = this.filterList[index];
      this.list.some((item, index) => {
        if(item.id === task.id) {
          originIndex = index;
        }
      });

      return originIndex;
    },

    doSearch(key) {
      this.searchKey = key;
    }
  }
}
</script>

