<template>
  <div class="c-switch" :class="{'is-checked': checked, 'is-disabled': disabled}" @click="switchValue">
    <span class="c-switch_core" ref="core" :style="{'width': coreWidth + 'px'}"></span>
  </div>
</template>

<script>
export default {
  name: 'CSwitch',

  props: {

    // 绑定的值
    value: {
      type: [Boolean, String, Number],
      default: ''
    },

    // 启用时的值
    activeValue: {
      type: [Boolean, String, Number],
      default: true
    },

    // 关闭时的值
    inactiveValue: {
      type: [Boolean, String, Number],
      default: false
    },

    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },

    // width
    width: {
      type: Number,
      default: 40
    },

    activeColor: {
      type: String,
      default: '#409EFF'
    },

    inactiveColor: {
      type: String,
      default: '#DCDFE6'
    }
  },

  data() {
    return {
      coreWidth: this.width
    };
  },

  created() {
    if (!~[this.activeValue, this.inactiveValue].indexOf(this.value)) {
      this.$emit('input', this.inactiveValue);
    }
  },

  computed: {
    checked() {
      return this.value === this.activeValue;
    }
  },

  watch: {
    checked() {

      // 更换颜色
      if (this.activeColor || this.inactiveColor) {
        this.setBgColor();
      }
    }
  },

  mounted() {
    if (this.activeColor || this.inactiveColor) {
      this.setBgColor();
    }
  },

  methods: {

    //设置颜色，在这操作dom比:style更灵活
    setBgColor() {
      let color = this.checked ? this.activeColor : this.inactiveColor;
      this.$refs.core.style.backgroundColor = color;
    },

    switchValue() {
      if (this.disabled) {
        return;
      }

      // 判断之前的状态
      const val = this.checked ? this.inactiveValue : this.activeValue;
      this.$emit('input', val);
      this.$emit('change', val);
    }
  }
};
</script>

<style lang="scss" scoped>
.c-switch {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}

.c-switch.is-disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.c-switch.is-checked .c-switch_core {
  background: #409eff;
}
.c-switch.is-checked .c-switch_core::after {
  left: 100%;
  margin-left: -17px;
}

.c-switch_core {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
  border: 1px solid #dcdfe6;
  outline: none;
  border-radius: 10px;
  box-sizing: border-box;
  background: #dcdfe6;
}
.c-switch_core::after {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  border-radius: 100%;
  transition: all .3s;
  width: 16px;
  height: 16px;
  background-color: #fff;
} 
</style>
