<template>
  <div class="c-collapse-item" 
    :class="{'is-active': isActive, 'is-disabled': disabled}">
    <div class="c-collapse-title_wrap">

      <div class="c-collapse-title_content" @click="toggleCollapse">
        <slot name="title"></slot>
      </div>

      <div class="c-collapse-title_options">
        <slot name="options"></slot>
      </div>

    </div>
    <c-collapse-transition>
      <div
        class="c-collapse-item_wrap"
        v-show="isActive">
        <div class="c-collapse-item_content">
          <slot></slot>
        </div>
      </div>
    </c-collapse-transition>

    <div class="c-collapse-icon_wrap"
      v-if="!disabled && icon"
      @click="toggleCollapse">
      <i class="e-collapse-icon iconfont icon-down"></i>
    </div>
  </div>
</template>

<script>
import CCollapseTransition from '@/components/common/transition/collapse-transition.vue';

export default {
  name: 'CCollapseItem',

  components: {CCollapseTransition},

  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    icon: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      isActive: false
    };
  },

  methods: {
    toggleCollapse() {
      if (this.disabled) {
        return;
      }

      this.isActive = !this.isActive;
      this.$emit('toggle-active', this.isActive);
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/styles/mixin.scss';
