$options-width: toRem(20px);

.c-collapse-item {
  position: relative;
  padding-bottom: toRem(20px);
  &.is-active {
    .c-collapse-title_wrap {
      border-bottom-color: transparent;
    }
    .e-collapse-icon {
      transform: rotateX(180deg);
    }
  }
  &.is-disabled {
    padding-bottom: 0;
  }

  .c-collapse-title_wrap {
    display: flex;
    align-items: stretch;
    min-height: toRem(40px);
    background-color: #fff;
    color: #303133;
    cursor: pointer;
    border-bottom: 1px solid $shadow-color;
    font-size: 13px;
    font-weight: 500;
    transition: border-bottom-color .3s;
    outline: none;
    .c-collapse-title_content {
      flex: 1;
    }
    .c-collapse-title_options {
      width: $options-width;
    }
  }

  .c-collapse-item_wrap {
    will-change: height;
    background-color: #fff;
    overflow: hidden;
    box-sizing: border-box;
    border-bottom: 1px solid #ebeef5;
    .c-collapse-item_content {
      padding: 0 toRem(10px) 0 toRem(10px);
      font-size: 13px;
      color: #303133;
