<template>
  <transition-group 
    appear
    @before-appear="beforeAppear"
    @appear="appear"
    :css="false"
    name="slide" 
    :tag="tag">
    <slot/>
  </transition-group>
</template>

<script>
import Velocity from 'velocity-animate';

export default {
  name: 'CSlideTransitionGroup',

  props: {
    tag: String
  },

  methods: {
    beforeAppear(el) {
      el.style.opacity = 0;
    },

    appear(el, done) {
      var delay = el.dataset.index * 50;
      setTimeout(() => {
        Velocity(el,
          {opacity: 1},
          {complete: done}
        );
      }, delay);
    }
  }
}
</script>

<style lang="scss" scoped>
.slide-leave-active {
    position: absolute;
    transition: all .2s;
}
.slide-enter, .slide-leave-to, .slide-appear {
    /*transform: translateX(-100%);*/
    opacity: 0;
}
</style>
