<template>
  <transition name="scale">
    <div class="add-win" v-show="show">
      <div class="add-win-inner">
        <div class="content">
          <div class="content-label">任务标题</div>
          <input class="content-title" :class="{'is-error': isError}" @blur="blurHandle" v-model="formData.title"/>
          <div class="content-label">任务描述</div>
          <textarea class="content-desc" v-model="formData.detail"></textarea>
          <div class="content-label">
            子任务 
            <button @click="addSubItem()">
              <i class="iconfont icon-add"></i>
            </button>
          </div>
          <complete-radio class="content-sub-item" 
                          size="small" 
                          v-for="(child, index) in formData.children" 
                          :key="index">
            <input class="content-title--sub" 
                   v-model="formData.children[index].text"/>
          </complete-radio>
        </div>

        <div class="options-wrap">
          <c-flex-bar>
            <i class="iconfont icon-repeat" slot="left"></i>
            <span>每日提醒</span>
            <c-switch
              slot="right"
              v-model="formData.repeat"
              :active-value="1"
              :inactive-value="0"/>
          </c-flex-bar>

          <c-collapse-item :icon="false">
            <c-flex-bar slot="title">
              <i class="iconfont icon-level" slot="left"></i>
              <c-tag 
                :text="activeLevel.text" 
                :color="activeLevel.color" 
              />
            </c-flex-bar>

            <section>
              <c-tag 
                :block="true"
                v-for="(value, name) in levels" 
                :key="value.id"
                :text="value.text"
                :color="value.color"
                @click.native="selectLevel(name)"/>
            </section>
          </c-collapse-item>

          <!-- <c-flex-bar>
            <i class="iconfont icon-tag" slot="left"></i>
            <c-tag text="学习"/>
          </c-flex-bar> -->
        </div>

        <div class="button-wrap">
          <button class="btn" @click="hide">取消</button>
          <button class="btn primary" @click="submitData">提交</button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import CompleteRadio from '@/components/common/complete-radio/index.vue';
import {TASK_LEVEL_MAP, TASK_LEVEL_KEY, STORAGE_KEY} from '@/assets/js/const';
import {setStorage, getStorage} from '@/assets/js/storage';

const DEFAULT_DATA = {
  title: '',
  detail: '',
  repeat: 0,
  level: 0,
  children: []
  // tags: []
};

export default {
  name: 'AddWin',

  components: {
    CompleteRadio
  },

  data () {
    return {
      isError: false,
      show: false,
      activeLevel: {},
      formData: {},
      isEdit: false
    }
  },
  
  created() {
    this.formData = this.deepClone(DEFAULT_DATA);
    this.levels = {};
    let startId = 0;
    for (let key in TASK_LEVEL_MAP) {
      this.levels[key] = Object.assign({id: startId++}, TASK_LEVEL_MAP[key]);
    }
    this.activeLevel = this.levels[TASK_LEVEL_KEY[0]];
  },

  methods: {
    edit(task) {
      this.editOriginData = task;
      this.isEdit = true;
      this.show = true;
      this.formData = task;
      this.activeLevel = this.levels[task.level];
    },
    
    add() {
      this.show = true;
      this.isEdit = false;
    },

    addSubItem () {
      this.formData.children.push({
        id: new Date().getTime(),
        text: '',
        isCompleted: false
      });
    },

    blurHandle() {
      if (!this.formData.title || !this.formData.title.trim()) {
        this.isError = true;
      } else {
        this.isError = false;
      }
    },

    hide() {
      this.show = false;

      // 清除数据
      this.formData = this.deepClone(DEFAULT_DATA);
      this.isError = false;
    },

    selectLevel(key) {
      this.formData.level = key;
      this.activeLevel = this.levels[key];
      this.showLevelList = false;
    },

    deepClone(val) {
      return JSON.parse(JSON.stringify(val));
    },

    submitData() {
      let errMsg = this.verify();
      if (errMsg) {
        return;
      }

      let list = getStorage(STORAGE_KEY);
      
      if (this.isEdit) {
        let index = '';
        let task = list.find((item, key) => {
          if (item.id === this.editOriginData.id) {
            index = key;
            return;
          }
        });
        
        task = {...task, ...this.formData};
        list[index] = task;
      } else {
        list.push({
          ...this.formData,
          id: new Date().getTime(),
          complete: 0,
          open: 0,
          createTime: new Date().getTime()
        });
      }

      setStorage(STORAGE_KEY, list);
      this.$emit('submit');
      this.hide();
    },

    verify() {
      let msg;
      if (!this.formData.title || !this.formData.title.trim()) {
        msg = '标题不能为空';
      }

      return msg;
    }
  }
}
</script>

